<template lang="html">
  <div>
    <code-box id="components-modal-demo-confirm" title="" :code="code" :description="description">
      <div slot="instance">
        <ant-button :on-click="showConfirm">
          
        </ant-button>
      </div>
    </code-box>
  </div>
</template>

<script>
import { Modal } from 'vue-ant-ui'
import { template as description } from './confirm.md'

export default {
  data: () => ({
    visible: false,
    description,
    code: `
      <template lang="html">
        <div>
          <ant-button :on-click="showConfirm">
            
          </ant-button>
        </div>
      </template>

      \<script\>
      import { Modal } from 'vue-ant-ui'

      export default {
        data: () => ({
          visible: false
        }),
        methods: {
          showConfirm () {
            Modal.confirm({
              title: '',
              content: '',
              onOk () {
                console.log('')
              },
              onCancel () {}
            })
          }
        }
      }
      \</script\>
    `
  }),
  methods: {
    showConfirm () {
      Modal.confirm({
        title: '',
        content: '',
        onOk () {
          console.log('')
        },
        onCancel () {}
      })
    }
  }
}
</script>



