<template lang="html">
  <div>
    <code-box id="components-spin-demo-inside" title="" :code="code" :description="description">
      <div slot="instance">
        <div>
          <ant-spin :spinning="loading">
            <ant-alert message=""
              description=""
              type="info"
            ></ant-alert>
          </ant-spin>
          <ant-switch :checked="loading" :on-change="toggle"></ant-switch>
        </div>
      </div>
    </code-box>
  </div>
</template>

<script>
import { template as description } from './nested.md'

export default {
  data: () => ({
    loading: false,
    description,
    code: `
      <template lang="html">
        <div>
          <div>
            <ant-spin :spinning="loading">
              <ant-alert message=""
                description=""
                type="info"
              ></ant-alert>
            </ant-spin>
            <ant-switch :checked="loading" :on-change="toggle"></ant-switch>
          </div>
        </div>
      </template>

      \<script\>
      export default {
        data: () => ({
          loading: false
        }),
        methods: {
          toggle (value) {
            this.loading = value
          }
        }
      }
      \</script\>
    `
  }),
  methods: {
    toggle (value) {
      this.loading = value
    }
  }
}
</script>



