<template lang="html">
  <div>
    <code-box id="components-message-demo-other" title="" :code="code" :description="description">
      <div slot="instance">
        <ant-button :on-click="success"></ant-button>
        <ant-button :on-click="error"></ant-button>
        <ant-button :on-click="warning"></ant-button>
      </div>
    </code-box>
  </div>
</template>

<script>
import { Message } from 'vue-ant-ui'
import { template as description } from './info.md'

export default {
  data: () => ({
    description,
    code: `
      <template lang="html">
        <div>
          <ant-button :on-click="success"></ant-button>
          <ant-button :on-click="error"></ant-button>
          <ant-button :on-click="warning"></ant-button>
        </div>
      </template>

      \<script\>
      import { Message } from 'vue-ant-ui'

      export default {
        methods: {
          success () {
            Message.success('')
          },
          error () {
            Message.error('')
          },
          warning () {
            Message.warning('')
          }
        }
      }
      \</script\>
    `
  }),
  methods: {
    success () {
      Message.success('')
    },
    error () {
      Message.error('')
    },
    warning () {
      Message.warning('')
    }
  }
}
</script>



