<template lang="html">
  <div>
    <code-box id="components-notification-demo-with-icon" title="" :code="code" :description="description">
      <div slot="instance">
        <ant-button :on-click="openNotificationWithIcon('success')"></ant-button>
        <ant-button :on-click="openNotificationWithIcon('info')"></ant-button>
        <ant-button :on-click="openNotificationWithIcon('warning')"></ant-button>
        <ant-button :on-click="openNotificationWithIcon('error')"></ant-button>
      </div>
    </code-box>
  </div>
</template>

<script>
import { Notification } from 'vue-ant-ui'
import { template as description } from './with-icon.md'

export default {
  data: () => ({
    description,
    code: `
      <template lang="html">
        <div>
          <ant-button :on-click="openNotificationWithIcon('success')"></ant-button>
          <ant-button :on-click="openNotificationWithIcon('info')"></ant-button>
          <ant-button :on-click="openNotificationWithIcon('warning')"></ant-button>
          <ant-button :on-click="openNotificationWithIcon('error')"></ant-button>
        </div>
      </template>

      \<script\>
      import { Notification } from 'vue-ant-ui'

      export default {
        methods: {
          openNotificationWithIcon (type) {
            return function () {
              Notification[type]({
                message: '',
                description: ''
              })
            }
          }
        }
      }
      \</script\>
    `
  }),
  methods: {
    openNotificationWithIcon (type) {
      return function () {
        Notification[type]({
          message: '',
          description: ''
        })
      }
    }
  }
}
</script>



