<template lang="html">
  <div>
    <code-box id="components-notification-demo-duration" title="" :code="code" :description="description">
      <div slot="instance">
        <ant-button type="primary" :on-click="openNotification"></ant-button>
      </div>
    </code-box>
  </div>
</template>

<script>
import { Notification } from 'vue-ant-ui'
import { template as description } from './basic.md'

export default {
  data: () => ({
    description,
    code: `
      <template lang="html">
        <div>
          <ant-button type="primary" :on-click="openNotification"></ant-button>
        </div>
      </template>

      \<script\>
      import { Notification } from 'vue-ant-ui'

      export default {
        methods: {
          openNotification () {
            Notification.open({
              message: '',
              description: ''
            })
          }
        }
      }
      \</script\>
    `
  }),
  methods: {
    openNotification () {
      Notification.open({
        message: '',
        description: ''
      })
    }
  }
}
</script>



